import React, { Component } from 'react';
import Header from './Header';
import Proyectos from './Proyectos';
import Servicios from './Servicios';
import Aprendizaje from './Aprendizaje';
import Contacto from './Contacto';

import logocs from '../img/carrionlogo.png';
import proofread from '../img/Proofread.jpg';
import articleWrite from '../img/article-write.jpg';

class App extends Component {
  state = {
    proyectos: [],
    servicios: [],
    aprendizaje: [],
  }

  componentDidMount() {
    this.cargarProyectos();
  }

  cargarProyectos = () => {
    let proyectos = [
      {
        nombre: 'Carrión Soluciones',
        img: logocs,
        descripcion: 'Sitio web de la empresa Carrión Soluciones, dedicada a la venta e instalación de puertas automatizadas, operadores y shutters.',
        url: 'http://www.carrionsoluciones.com'
      }
    ];

    let servicios = [
      {
        nombre: 'Desarrollo de aplicación web',
        img: proofread,
        descripcion: 'Desarrollo de sitio o aplicación web completa con OOP PHP, Laravel o ReactJS',
        url: '#'
      },
      {
        nombre: 'Corrección de BackEnd',
        img: proofread,
        descripcion: 'Corrección de cualquier función o parte de BackEnd de un sitio o aplicación web desarrollado en PHP o Laravel',
        url: '#'
      },
      {
        nombre: 'Redacción de artículo',
        img: proofread,
        descripcion: 'Redacción de artículo de cualquier tema',
        url: 'https://www.fiverr.com/harverbo/write-a-400-or-more-words-spanish-article'
      },
      {
        nombre: 'Corrección de artículo o documento',
        img: articleWrite,
        descripcion: 'Corrección de errores de cualquier artículo o documento en español',
        url: 'https://www.fiverr.com/harverbo/proofread-or-edit-your-spanish-document'
      },
    ];

    let aprendizaje = [
      {
        nombre: 'Desarrollo Web Completo',
        img: logocs,
        descripcion: 'Desarrollo Web Completo con HTML5, CSS3, JavaScript, JQuery, Ajax, PHP y MySQL',
        url: 'https://www.udemy.com/certificate/UC-H16XIP7C/'
      }
    ];

    this.setState({
      proyectos,
      servicios,
      aprendizaje,
    });
  }

  render(){
    return (
      <div className="App">
        <Header
          titulo='Harold Abreu Ramírez'
        />

        <Proyectos
          proyectos = {this.state.proyectos}
        />

        <Servicios
          servicios = {this.state.servicios}
        />

        <Aprendizaje
          aprendizaje = {this.state.aprendizaje}
        />

        <Contacto/>
      </div>
    );
  }
}

export default App;
